import React, {useState} from 'react';
import {NavLink} from "react-router-dom";
import TimerHeadContainer from "../Timer/TimerContainer";
import {logout} from "../../redux/authReducer";
import {connect} from "react-redux";


const Header = (props) => {
    const [show, setState] = useState(false);

    function toggle() {
        setState(!show);
    }
    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark" role="navigation">
            <div className="container justify-content-between align-content-start">
                <div className="menu">
                <button className="navbar-toggler" onClick={toggle}>
                    <span className="ti-view-list"></span>
                </button>
                <div className={show ? 'menu bg-dark position-absolute p-3 collapse navbar-collapse show'
                    : 'collapse navbar-collapse'}  >
                    <ul className="navbar-nav">
                        <li className="nav-item"><NavLink to="/" className=" nav-link">
                            <span className="ti-user"></span> Профиль</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink to="/orders" className="nav-link">
                                <span className="ti-layers"></span> Накладные</NavLink>
                        </li>
                    </ul>
                </div>
                </div>
                <TimerHeadContainer />
                {props.isAuth ? <div className="pl-3"><span onClick={props.logout} className="btn btn-info small ti-unlock btn-sm"></span></div>
                    : ''}

            </div>
        </nav>

    );
}
let mapStateToProps = (state) => {
    return {
        isAuth:state.auth.isAuth
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        logout: () => {
            localStorage.clear();
            dispatch(logout());
        }
    }
}

export default connect (mapStateToProps,mapDispatchToProps)(Header)

