import React from 'react';
import store from './redux/reduxStore'
import {BrowserRouter, Route, Switch} from "react-router-dom";
import {Provider} from "react-redux";
import HomeContainer from "./components/Home/HomeContainer";
import Login from "./components/Login/Login";
import withSuspense from "./hok/withSuspense";

const OrdersContainer = React.lazy(() => import('./components/Orders/OrdersContainer'));
const OrderContainer = React.lazy(() => import('./components/Order/OrderContainer'));

const App = (props) => {
    return (
        <Switch>
            <Route path="/" exact render={() => <HomeContainer/>}/>
            <Route path="/login" render={() => <Login/>}/>
            <Route path="/orders" render={withSuspense(OrdersContainer)}/>
            <Route path="/order/:orderId" render={withSuspense(OrderContainer)}/>
        </Switch>
    );
}

const MainApp = (props) => {
    return <BrowserRouter>
        <Provider store={store}>
            <App/>
        </Provider>
    </BrowserRouter>
}


export default MainApp;
