export const localToken = localStorage.getItem('token');

export const parseJwt = (token = localToken) => {
    try {
        return JSON.parse(atob(token.split('.')[1]));
    } catch (e) {
        return null;
    }
};

export const parseJwtFromTokenTime = (token = localToken) => {
    try {
        return JSON.parse(atob(token.split('.')[1])).exp;
    } catch (e) {
        return null;
    }
};
export const parseJwtFromTokenFirstName = () => { return localStorage.getItem('firstName')
};
export const parseJwtFromTokenRole = (token = localToken) => {
    try {
        return JSON.parse(atob(token.split('.')[1])).role;
    } catch (e) {
        return null;
    }
};