import * as axios from "axios";

const instance = axios.create({
    baseURL: 'http://109.86.230.142:50000',
    timeout: 5000,
    headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token'),
        'Content-Type': 'application/json'
    }
});


export const authAPI = {
    login(formData) {
        return instance.post('users/authenticate', {...formData});
    }
};
export const ordersAPI = {
    getOrders() {
        return instance.get('orders').then(
            responce => {
                return responce.data;
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
    },
    getOrder(consignationId) {
        return instance.get('orders/' + consignationId).then(
            responce => {
                return responce.data;
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
    },
    getOrderItem(consignationId) {
        return instance.get('orders/item' + consignationId).then(
            responce => {
                return responce.data;
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
    },
};
