import React from 'react';
import Header from '../Header/Header';
import {parseJwtFromTokenFirstName, parseJwtFromTokenRole} from "../../utils/jwt";
const Home = (props) => {
    return(
        <div className="home-page site-wrap">
            <Header />
            <div className="container">
                <h1 className="my-3">Привет, {props.firstName ? props.firstName : parseJwtFromTokenFirstName()}!</h1>
                <h3>Ваша роль: {props.role ? props.role : parseJwtFromTokenRole()}</h3>
            </div>
        </div>

    )
};
export default Home;