import {ordersAPI} from "../api/api";




const GET_ORDERS = 'GET_ORDERS';
const GET_ORDER = 'GET_ORDER';


let initialState = {
    orders: [],
    order:[]
};

const orderReducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_ORDERS:
            return {
                ...state,
                orders:action.payload

            }
        case GET_ORDER:
            return {
                ...state,
                order:action.payload

            }
        default:
            return state;
    }
};


export const getOrdersAC = (data) => ({type: GET_ORDERS, payload: data});
export const getOrderAC = (data) => ({type: GET_ORDER, payload: data});


export const allOrders = () => async (dispatch) => {
    let data = await ordersAPI.getOrders();
    dispatch(getOrdersAC(data));
};
export const getOrderApi = (consignationId) => async (dispatch) => {
    let data = await ordersAPI.getOrder(consignationId);
    dispatch(getOrderAC(data));
};

export default orderReducer;